@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.container-login{display:flex;flex-direction:column;justify-content:center;min-width:360px}.login-logo{text-align:center;margin:50px auto 0;background-image:url(/static/media/logo.6ffb2fec.jpeg);background-repeat:no-repeat;background-size:contain;min-height:200px;max-height:200px;min-width:200px;max-width:200px}.login-page{width:360px;padding:8% 0 0;margin:auto}.form{position:relative;z-index:1;background:#f0990b;background:linear-gradient(to left, #f0990b, #f8b446);max-width:360px;margin:0 auto 100px;padding:45px;text-align:center;box-shadow:0 0 20px 0 rgba(0,0,0,0.2),0 5px 5px 0 rgba(0,0,0,0.24);border-radius:10%}.form input{font-family:"Roboto", sans-serif;outline:0;background:#f2f2f2;width:100%;border:0;margin:0 0 15px;padding:15px;box-sizing:border-box;font-size:14px}.form button{font-family:"Roboto", sans-serif;font-weight:bold;border-radius:5px;box-shadow:0 0 20px 0 rgba(0,0,0,0.2),0 5px 5px 0 rgba(0,0,0,0.24);background:#14509d;text-transform:uppercase;outline:0;width:100%;border:0;padding:15px;color:#fff;font-size:14px;transition:all 0.3 ease;cursor:pointer}.form button:hover,.form button:active,.form button:focus{background:#294b76}.form .message{margin:15px 0 0;color:#000;font-size:12px}.form .message a{color:#14509d;text-decoration:none;font-weight:bold}.container-login{position:relative;z-index:1;max-width:300px;margin:0 auto}.container-login:before,.container-login:after{content:"";display:block;clear:both}.container-login .info{margin:50px auto;text-align:center}.container-login .info h1{margin:0 0 15px;padding:0;font-size:36px;font-weight:300;color:#000}.container-login .info span{color:#4d4d4d;font-size:12px}.container-login .info span a{color:#000;text-decoration:none}.container-login .info span .fa{color:#ef3b3a}.error{color:#db2269;font-size:0.9em;font-weight:bold;display:relative}body{background:#fff;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.Principal{color:#000;display:flex;min-width:100vw}.leftSide{min-width:15%}.rightSide{min-width:15%}.centerContent{min-width:70%}@media (max-width: 768px){.leftSide{max-width:5%;min-width:5%}.rightSide{max-width:5%;min-width:5%}.centerContent{min-width:90%}}

.calendar-title-month{display:flex;justify-content:center;align-items:center;font-size:2em;margin:0.25em}.calendar-month{width:90%;margin:0 auto;padding:20px;background:#fff;list-style-type:none;font-weight:bolder;font-family:Arial, sans-serif;font-size:12px;padding:50px 0;margin:0;background:#eee}.calendar-month .week{display:flex;flex-direction:row;flex-wrap:wrap}.calendar-month .week__header{font-weight:bold;order:1;background:#fff;border-right:1px solid #e8e8e8;text-align:center;padding-top:20px;padding-bottom:20px;text-transform:uppercase;color:#333;letter-spacing:2px;width:14.2857%;box-sizing:border-box}.calendar-month .week__header:nth-child(7){border-right:0}.calendar-month .week__header:first-letter{font-size:12px}@media (max-width: 767px){.calendar-month .week__header{padding:10px 0;font-size:0}}.calendar-month .days{list-style-type:none;margin:0;padding:0;display:flex;width:100%}.calendar-month .day{position:relative;order:1;width:14.2857%;background:#fff;border-right:1px solid #e8e8e8;border-top:1px solid #e8e8e8;transition:0.5s;box-sizing:border-box}.calendar-month .day:nth-child(7n+7){border-left:none}.calendar-month .day:nth-child(7n+7){border-right:none}.calendar-month .day:after{content:"";display:block;padding-bottom:100%}.calendar-month .day:hover{background:#eee}.calendar-month .day__number{position:absolute;right:5px;top:5px;margin:0;font-weight:normal}@media (max-width: 767px){.calendar-month .day__number{right:5px;top:5px}}.calendar-month .day--empty{background:#f8f8f8}.calendar-month .day--empty:hover{cursor:default;background:#f8f8f8}.calendar-month a{display:block;width:100%;height:100%}

.cards{display:flex;justify-content:space-between;flex-wrap:no-wrap}.card{border:1px solid #000000;border-radius:15px;min-height:10em;box-shadow:2px 2px 10px #666;padding:2em;margin:1em;flex-grow:0;flex-shrink:1;flex-basis:calc(25% - 2em)}@media (max-width: 768px){.cards{flex-wrap:wrap}.card{flex-grow:0;flex-shrink:1;flex-basis:calc(50% - 2em)}}@media (max-width: 360px){.cards{display:flex;flex-wrap:wrap;justify-content:space-between}.card{flex-grow:0;flex-shrink:1;flex-basis:calc(100% - 1em)}}

@font-face{font-family:DancingScript;src:url(//fonts.googleapis.com/css?family=Dancing+Script)}h1{margin:0.25em;font-family:"Dancing Script";font-size:15px;font-style:italic;-webkit-font-feature-settings:normal;font-feature-settings:normal;font-variant:normal;font-weight:700;line-height:26.4px}

.icon{color:#fff;font-size:32px;cursor:pointer}.icon:hover{color:#f2f2f2}

*{margin:0;padding:0;box-sizing:border-box}a{text-decoration:none}li{list-style:none}.navbar{display:flex;align-items:center;justify-content:space-between;padding:20px;background-color:#f0990b;color:#fff}.nav-links a{color:#fff}.logo{font-size:32px}.menu{display:flex;grid-gap:1em;gap:1em;font-size:18px}.menu li:not(.nohover):hover{background-color:#4782ce;border-radius:5px;transition:0.3s ease}.menu li{padding:5px 14px}.services{position:relative}.dropdown{background-color:#4782ce;padding:1em 0;position:absolute;display:none;border-radius:8px;top:32px}.dropdown li+li{margin-top:10px}.dropdown li{padding:0.5em 1em;width:8em;text-align:center}.dropdown li:not(.nohover):hover{background-color:#6b97ce}.services:hover .dropdown{display:block}input[type="checkbox"]{display:none}.hamburger{display:none;font-size:24px;-webkit-user-select:none;-ms-user-select:none;user-select:none}@media (max-width: 768px){.menu{display:none;position:absolute;background-color:#f0990b;right:0;left:0;text-align:center;padding:16px 0}.menu li:hover{display:inline-block;background-color:#4782ce;transition:0.3s ease}.menu li+li{margin-top:12px}input[type="checkbox"]:checked ~ .menu{display:block}.hamburger{display:block}.dropdown{left:50%;top:30px;-webkit-transform:translateX(35%);transform:translateX(35%)}.dropdown li:hover{background-color:#6b97ce}}

.btn{background:#14509d;border:1px solid #14509d;border-radius:6px;box-shadow:rgba(0,0,0,0.1) 1px 2px 4px;box-sizing:border-box;color:#ffffff;cursor:pointer;display:inline-flex;justify-content:center;align-items:center;font-family:nunito, roboto, proxima-nova, "proxima nova", sans-serif;font-size:16px;font-weight:800;line-height:16px;min-height:40px;outline:0;padding:12px 14px;margin:0.15em;text-align:center;text-rendering:geometricprecision;text-transform:none;-ms-user-select:none;user-select:none;-webkit-user-select:none;touch-action:manipulation;vertical-align:middle}.btn:hover,.btn:active{background-color:transparent;background-color:initial;background-position:0 0;color:#14509d}.btn:active{opacity:0.5}.btn-secondary-a{background:#f0bf0b;border:1px solid #f0bf0b;color:#3d3d3d}.btn-secondary-a:hover,.btn-secondary-a:active{color:#3d3d3d}.btn-secondary-b{background:#f0600b;border:1px solid #f0600b;color:#fff}.btn-secondary-b:hover,.btn-secondary-b:active{color:#3d3d3d}.btn-complementary{background:#14509d;border:1px solid #14509d;color:#fff}.btn-complementary:hover,.btn-complementary:active{color:#3d3d3d}

.modal{position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.4)}.modal-header{padding:2px 16px;background-color:#f0990b;color:white}.modal-body{padding:2px 16px;margin-top:0.5em;margin-bottom:0.5em;min-height:200px}.modal-footer{padding:2px 16px;background-color:#f0990b;color:white}.modal-content{position:relative;background-color:#fefefe;margin:auto;padding:0;border:0 solid #888;border-radius:17px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:400px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);-webkit-animation-name:animatetop;animation-name:animatetop;-webkit-animation-duration:0.4s;animation-duration:0.4s;height:auto;top:35%}.modal-content>p{padding:1em}.modal-header{border-radius:10px 10px 0 0;padding:1em}.modal-footer{border-radius:0 0 10px 10px;padding:1em}@-webkit-keyframes animatetop{from{top:-300px;opacity:0}to{top:35%;opacity:1}}@keyframes animatetop{from{top:-300px;opacity:0}to{top:35%;opacity:1}}.close{color:#131313;float:right;font-size:28px;font-weight:bold}.close:hover,.close:focus{color:black;text-decoration:none;cursor:pointer}.display-block{display:block}.display-none{display:none}

.noteBody{font-family:"Indie Flower", cursive;font-weight:bold}

